<template>
  <nav class="green darken-4">
    <div class="nav-wrapper">
      <ul id="nav-mobile" class="left hide-on-med-and-down">
        <!--suppress JSUnresolvedReference -->
        <li v-for="item in navMenuItems" :key="item.path"
            :class="{active: currentPath === item.path}"
            @click.prevent="changeActiveClass">
          <router-link v-if="item.isAuthRequired === !!auth.currentUser" :to="item.path">
            {{ item.name }}
            <span v-if="item.name === 'Счета'">| {{ currencyFilter(totalBalance) }}</span>
          </router-link>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script lang="ts" setup>
import { reactive, ref } from 'vue';
import { useBalanceStore } from '@/stores/balance';
import { auth } from '@/utils/firebase.config';
import { storeToRefs } from 'pinia';
import { useFiltersStore } from '@/stores/filters';

const balanceStore = useBalanceStore();
const { totalBalance } = storeToRefs(balanceStore);
const { currencyFilter } = useFiltersStore();

const navMenuItems = reactive([
  {
    name: 'Главная',
    path: '/',
    isAuthRequired: true
  },
  {
    name: 'Календарь',
    path: '/calendar',
    isAuthRequired: true
  },
  {
    name: 'Планирование',
    path: '/planning',
    isAuthRequired: true
  },
  {
    name: 'Операции',
    path: '/records',
    isAuthRequired: true
  },
  {
    name: 'Категории',
    path: '/categories',
    isAuthRequired: true
  },
  {
    name: `Счета`,
    path: '/accounts',
    isAuthRequired: true
  },
  {
    name: auth.currentUser?.displayName || 'Профиль',
    path: '/profile',
    isAuthRequired: true
  },
  {
    name: 'Регистрация',
    path: '/register',
    isAuthRequired: false
  },
  {
    name: 'Войти',
    path: '/login',
    isAuthRequired: false
  }
]);

// noinspection JSUnusedGlobalSymbols
const currentPath = ref(location.pathname);

const changeActiveClass = (event: any) => {
  if (!!event.target.offsetParent) {
    event.target.offsetParent.querySelectorAll('li').forEach((item: any) => {
      item.classList.remove('active');
    });
    event.target.parentElement.classList.add('active');
  } else {
    setTimeout(() => changeActiveClass, 50);
  }
};
</script>

<style lang="scss" scoped>
.brand-logo {
  top: 5px;
  left: 20px;

  @media only screen and (max-width: 992px) {
    left: 50%;
  }

  img {
    max-height: 54px;
    object-fit: contain;

    @media only screen and (max-width: 992px) {
      max-height: 46px;
    }

  }
}
</style>